.App {
  text-align: center;
  font-size: 12px;
  font-family: Helvetica, Dotum, "돋움", Apple SD Gothic Neo, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5svh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  height: 50px;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

img {
  width: 25px;
  height: 22px;
  padding-right: 8px;
}

.gray-box {
  background-color: lightgray;
  font-size: 12px;
  padding: 3px;
}
.coral-box {
  background-color: rgb(231, 174, 172);
  font-size: 12px;
  padding: 1%;
  margin: 0;
}

.reward {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid rgb(240, 212, 194);
}

.container {
  padding-left: 30px;
  padding-right: 30px;
  margin-bottom: 30px;
}

.tournament {
  display: flex;
  justify-content: space-around;
  background-color: lightgoldenrodyellow;
  align-items: center;
  padding: 5px;
}

.indo {
  margin-left: 10px;
  padding: 3px;
  background-color: rgb(93, 155, 236);
}
.an {
  margin-left: 10px;
  padding: 3px;
  background-color: orange;
}
.jin {
  margin-left: 10px;
  padding: 3px;
  background-color: rgb(213, 162, 247);
}
.jun {
  margin-left: 10px;
  padding: 3px;
  background-color: rgb(140, 214, 140);
}
.yeol {
  margin-left: 10px;
  padding: 3px;
  background-color: rgb(216, 238, 121);
}
